/* mulish-200 - latin-ext_latin */
@font-face {
	font-family: "Mulish";
	font-display: swap;
	font-style: normal;
	font-weight: 200;
	src: url("./mulish-v7-latin-ext_latin-200.eot"); /* IE9 Compat Modes */
	src: local(""),
		url("./mulish-v7-latin-ext_latin-200.eot?#iefix")
			format("embedded-opentype"),
		/* IE6-IE8 */ url("./mulish-v7-latin-ext_latin-200.woff2") format("woff2"),
		/* Super Modern Browsers */ url("./mulish-v7-latin-ext_latin-200.woff")
			format("woff"),
		/* Modern Browsers */ url("./mulish-v7-latin-ext_latin-200.ttf")
			format("truetype"),
		/* Safari, Android, iOS */ url("./mulish-v7-latin-ext_latin-200.svg#Mulish")
			format("svg"); /* Legacy iOS */
}
